form {
  display: flex;
  padding: 10px;
}
.wrapper {
  font-family: 'Itim', cursive;
  font-size: larger;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('./Images/bg.jpg');
  background-position: center ;
  background-repeat: no-repeat;
  background-size: cover;
}
.input {
  margin-right: 10px;
}
.frame {
  width: 40vw;
  max-width: 400px;
}
.header {
  display: inline;
  text-align: center;
}
.list-wrapper {
  max-height: 200px;
  overflow-y: auto;
}

